/* ===================================================================
   RESET & GLOBAL STYLES
=================================================================== */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  /* Set default font and colors for the body */
  body {
    font-family: "Brush Script MT", "Lucida Handwriting", "Apple Chancery", cursive;
    line-height: 1.6;
    background: #f4f4f4;
    color: #333;
  }
  
  /* Use Arial for all slide content */
  .card-text,
  .project-text,
  .work-text,
  .vol-text,
  .edu-text {
    font-family: Arial, sans-serif;
  }
  
  /* Header Titles */
  h1, h2, h3 {
    font-family: "Brush Script MT", "Lucida Handwriting", "Apple Chancery", cursive;
  }
  
  /* Hover Effect for All Slides */
  .bio-slide:hover,
  .project-slide:hover,
  .work-slide:hover,
  .vol-slide:hover,
  .edu-slide:hover,
  .gallery-slide:hover {
    transform: translateY(-2px);
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.4);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  /* ===================================================================
     HEADER & NAVIGATION
  =================================================================== */
  header {
    background: #333;
    color: #fff;
    padding: 0 20px;
    height: 60px;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
    display: flex;
    align-items: center;
  }
  
  .navbar {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
  }
  
  /* Hamburger Menu for Mobile */
  .menu-toggle {
    position: absolute;
    left: 20px;
    top: 50%;
    transform: translateY(-50%);
    display: none;
    flex-direction: column;
    cursor: pointer;
  }
  
  .menu-toggle .bar {
    height: 3px;
    width: 25px;
    background-color: #fff;
    margin: 4px 0;
  }
  
  /* Navigation Menu */
  .nav {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0 auto;
  }
  
  .nav li {
    margin: 0;
  }
  
  .nav li a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    padding: 10px 15px;
    transition: color 0.3s ease;
  }
  
  .nav li a:hover {
    text-decoration: underline;
    color: #ddd;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 4px;
  }
  
  /* ===================================================================
     SECTION TITLES & LAYOUT
  =================================================================== */
  section h2,
  section h3 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  section h2 {
    font-size: 3rem;
  }
  
  section h3 {
    font-size: 2rem;
  }
  
  section {
    padding: 20px;
    margin-top: 60px;
    scroll-margin-top: 80px;
  }
  
  /* ===================================================================
     BIO SECTION
  =================================================================== */
  /* Bio Intro */
  .bio-intro {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    font-size: 2rem;
  }
  
  .hand-wave {
    width: 80px;
    height: auto;
  }
  
  /* Bio Slider */
  .bio-slider {
    width: 90%;
    max-width: 800px;
    margin: 0 auto 5px;
    height: 700px;
    position: relative;
    overflow: hidden;
  }
  
  /* Individual Bio Slide */
  .bio-slide {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    background: #fff;
    display: none;
  }
  
  .bio-slide.active {
    display: flex;
  }
  
  /* Card Image (75% height) */
  .card-image {
    width: 100%;
    height: 75%;
    overflow: hidden;
  }
  
  .card-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
  }
  
  /* Card Text (25% height) */
  .card-text {
    height: 25%;
    padding: 5px;
    font-size: 16px;
    overflow: auto;
    line-height: 1.4;
    background: #f9f9f9;
  }
  
  /* Bio Navigation Buttons */
  .bio-nav {
    text-align: center;
    margin-top: 5px;
  }
  
  .bio-nav button {
    display: inline-block;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .bio-nav button:hover {
    background: #555;
  }
  
  /* ===================================================================
     PROJECTS SECTION
  =================================================================== */
  .project-slider-container {
    position: relative;
    width: 90%;
    max-width: 800px;
    margin: 0 auto 40px;
  }
  
  .project-slider {
    overflow: hidden;
    margin-bottom: 10px;
    height: 600px;
    position: relative;
  }
  
  .project-slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    background: #fff;
    display: none;
  }
  
  .project-slide.active {
    display: flex;
    flex-direction: column;
  }
  
  .project-image {
    flex: 1 1 60%;
    overflow: hidden;
    max-height: 300px;
  }
  
  .project-image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    object-position: center;
    display: block;
  }
  
  /* Project Text */
  .project-text {
    display: flex;
    flex-direction: column;
    padding: 20px;
    font-size: 16px;
    line-height: 1.5;
    background: #f9f9f9;
    text-align: left;
  }
  
  .project-text a.project-link {
    margin-top: auto;
  }
  
  .project-text a {
    display: block;
    text-align: center;
    margin-top: 10px;
  }
  
  .project-text ul,
  .card-text ul {
    list-style-type: disc;
    list-style-position: inside;
    padding-left: 0;
    margin-left: 0;
  }
  
  .project-text ul li,
  .card-text ul li {
    margin-bottom: 5px;
  }
  
  .tech-logos {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
  }
  
  .tech-logos img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  
  .project-link {
    padding: 8px 15px;
    background: #333;
    color: #fff;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .project-link:hover {
    background: #555;
  }
  
  /* Project Navigation Buttons */
  .project-nav {
    text-align: center;
    margin-top: 5px;
  }
  
  .project-nav button {
    display: inline-block;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .project-nav button:hover {
    background: #555;
  }
  
  /* ===================================================================
     WORK & VOLUNTEERING SECTION
  =================================================================== */
  .work-slider-container,
  .vol-slider-container {
    position: relative;
    width: 90%;
    max-width: 800px;
    margin: 0 auto 40px;
    height: 500px;
  }
  
  .work-slider,
  .vol-slider {
    position: relative;
    height: 100%;
    overflow: hidden;
  }
  
  .work-slide,
  .vol-slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    display: none;
  }
  
  .work-slide.active,
  .vol-slide.active {
    display: block;
  }
  
  /* Work Section */
  .round-logo {
    display: block;
    margin: 20px auto 10px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
  
  .work-slide h3 {
    margin-top: 10px;
    margin-bottom: 10px;
    text-align: center;
  }
  
  .work-text p,
  .work-text ul {
    margin: 10px 0;
    text-align: left;
  }
  
  .work-text ul {
    padding-left: 20px;
    list-style-type: disc;
  }
  
  /* Work Navigation Buttons */
  .work-nav {
    text-align: center;
    margin-top: 5px;
  }
  
  .work-nav button {
    display: inline-block;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .work-nav button:hover {
    background: #555;
  }
  
  /* Experience Button */
  .experience-button {
    display: block;
    margin: 10px auto;
    padding: 8px 16px;
    background-color: #333;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
    font-size: 14px;
    transition: background-color 0.3s ease;
    text-align: center;
  }
  
  .experience-button:hover {
    background-color: #555;
  }
  
  .text-content {
    margin-bottom: 40px;
  }
  
  .work-text,
  .vol-text {
    position: absolute;
    top: 140px;
    left: 0;
    right: 0;
    bottom: 40px;
    padding: 20px;
    background: rgba(249, 249, 249, 0.9);
    overflow-y: hidden;
  }
  
  /* Volunteering Navigation Buttons */
  .vol-nav {
    text-align: center;
    margin-top: 5px;
  }
  
  .vol-nav button {
    display: inline-block;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .vol-nav button:hover {
    background: #555;
  }
  
  /* ===================================================================
     EDUCATION SECTION
  =================================================================== */
  .edu-slider {
    position: relative;
    width: 90%;
    max-width: 800px;
    margin: 0 auto 40px;
    aspect-ratio: 1200 / 850;
    overflow: hidden;
    border-radius: 15px;
  }
  
  .edu-slide {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
  }
  
  .edu-slide.active {
    display: block;
  }
  
  .edu-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
    border-radius: 15px;
  }
  
  .edu-text {
    position: absolute;
    bottom: 20px;
    left: 20px;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    line-height: 1.4;
  }
  
  /* ===================================================================
     GALLERY SECTION
  =================================================================== */
  .gallery-slider-container {
    position: relative;
    width: 90%;
    max-width: 800px;
    margin: 0 auto 40px;
    height: 300px;
    background: transparent;
    border-radius: 0;
    overflow: visible;
  }
  
  .gallery-slider {
    position: relative;
    height: 100%;
  }
  
  .gallery-slide {
    position: absolute;
    width: 30%;
    height: 100%;
    transition: all 0.5s ease;
    display: none;
  }
  
  .gallery-slide.left {
    left: 0;
    top: 20px;
    display: block;
    opacity: 0.7;
    -webkit-mask-image: linear-gradient(to right, transparent 0%, transparent 45%, black 55%, black 100%);
    mask-image: linear-gradient(to right, transparent 0%, transparent 45%, black 55%, black 100%);
  }
  
  .gallery-slide.active {
    left: 35%;
    top: 0;
    z-index: 2;
    display: block;
    opacity: 1;
    -webkit-mask-image: none;
    mask-image: none;
  }
  
  .gallery-slide.right {
    left: 70%;
    top: 20px;
    display: block;
    opacity: 0.7;
    -webkit-mask-image: linear-gradient(to left, transparent 0%, transparent 45%, black 55%, black 100%);
    mask-image: linear-gradient(to left, transparent 0%, transparent 45%, black 55%, black 100%);
  }
  
  .gallery-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    display: block;
    border-radius: 10px;
    margin-bottom: -5px;
  }
  
  .gallery-slide.left img,
  .gallery-slide.right img {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    margin-bottom: -5px;
  }
  
  .gallery-slide.left,
  .gallery-slide.right {
    overflow: visible;
    top: 10px;
  }
  
  .gallery-nav {
    text-align: center;
    margin-top: 10px;
  }
  
  .gallery-nav button {
    display: inline-block;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    border: none;
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 10px;
    font-size: 18px;
    transition: background-color 0.3s ease;
  }
  
  .gallery-nav button:hover {
    background: #555;
  }
  
  .gallery-caption {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    margin-top: 15px;
    color: #000;
    font-family: "Brush Script MT", "Lucida Handwriting", "Apple Chancery", cursive;
    font-size: 1.5rem;
  }
  
  /* ===================================================================
     RESUME SECTION
  =================================================================== */
  #resume {
    text-align: center;
    padding: 40px 20px;
  }
  
  .resume-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .resume-link {
    display: inline-block;
    margin-top: 15px;
    padding: 12px 20px;
    background: #333;
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    border-radius: 5px;
    transition: background 0.3s ease;
  }
  
  .resume-link:hover {
    background: #555;
  }
  
  /* ===================================================================
     BACKGROUND VIDEO
  =================================================================== */
  #bg-video {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  
  /* ===================================================================
     FOOTER
  =================================================================== */
  footer {
    background: #333;
    color: #fff;
    text-align: center;
    padding: 20px 0;
    margin-top: 40px;
    width: 100%;
  }
  
  .social-media {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    padding: 10px;
  }
  
  .social-media a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    display: flex;
    align-items: center;
    gap: 8px;
    transition: color 0.3s ease;
  }
  
  .social-media a:hover {
    color: #ddd;
  }
  
  .social-media i {
    font-size: 22px;
  }
  
  /* ===================================================================
     ROTATING BEARING (Languages & Frameworks Section)
  =================================================================== */
  .rotating-bearing {
    position: relative;
    width: 400px;  /* Increased from 300px */
    height: 400px; /* Increased from 300px */
    margin: 0 auto;  /* Center the container */
    overflow: hidden; /* Hide logos outside the container */
  }
  
  .bearing-logo-wrapper {
    position: absolute;
  }
  
  .bearing-logo {
    width: 60px;
    height: 60px;
    object-fit: contain;
  }
  
  /* ===================================================================
     SECTION BOX (for Languages & Frameworks Section)
  =================================================================== */
  .section-box {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 60px;
    padding-bottom: 60px;
    margin: 20px auto;
    max-width: 800px;
    overflow: visible;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .section-box:hover {
    transform: translateY(-3px);
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.4);
  }
  
  /* ===================================================================
     RESPONSIVE STYLES
  =================================================================== */
  @media (max-width: 768px) {
    header {
      height: 60px;
    }
    
    .menu-toggle {
      display: flex;
    }
    
    .nav {
      display: none;
      flex-direction: column;
      width: 100%;
      background: #333;
      position: absolute;
      top: 60px;
      left: 0;
      z-index: 999;
      border-radius: 8px;
    }
    
    .nav.active {
      display: flex;
    }
    
    .nav li {
      margin: 10px 0;
      text-align: center;
    }
    
    .navbar {
      justify-content: space-between;
      padding: 0 20px;
    }
    
    .social-media {
      flex-direction: column;
      align-items: center;
    }
    
    .work-text, .vol-text {
      top: 120px;
      bottom: 20px;
      padding: 25px;
      overflow-y: auto;
    }
  }
  
  @media (max-width: 600px) {
    .work-text,
    .vol-text {
      padding-bottom: 40px;
      overflow-y: auto;
    }
    
    .bio-slider {
      height: 450px;
    }
    
    .card-image {
      height: 75%;
    }
    
    .card-text {
      height: 25%;
      padding: 2px;
      font-size: 14px;
    }
    
    section h1 {
      font-size: 2.7rem;
    }
    
    .gallery-slide.left {
      left: 0;
    }
    
    .gallery-slide.active {
      left: 34%;
    }
    
    .gallery-slide.right {
      left: 68%;
    }
    
    .rotating-bearing {
      transform: translateX(-10%);
    }
  }
  